<template>
  <div class="table">
    <div class="tool-bar">
      <div class="search">
        <el-input class="search-input" size="mini" placeholder="请输入内容" prefix-icon="el-icon-search" v-model="searchString" clearable>
        </el-input>
        <el-button type="primary" size="mini" icon="el-icon-search">查询</el-button>
      </div>
      <!--<el-button type="primary" plain @click="getDataVolume()">查询数据量</el-button>-->
      <!--<el-button type="primary" plain @click="addToDevelopList()">加入开发列表</el-button>-->
      <el-dropdown @command="handleCommand">
      <span class="el-dropdown-link">
        操作列表<i class="el-icon-arrow-down el-icon--right"></i>
      </span>
        <el-dropdown-menu slot="dropdown">
          <el-dropdown-item command="" icon="el-icon-plus">批量开发</el-dropdown-item>
          </el-dropdown-menu>
      </el-dropdown>
    </div>
    <hr>
    <el-table
      :data="toBeDevelopList"
      height="90%"
      style="width: 100%"
      @row-click="clickRow"
      ref="toBeDevelopList"
      @selection-change="handleSelectionChange"
      :cell-style="cellStyle"
      :header-cell-style="headerCellStyle">
      <el-table-column type="expand">
        <template slot-scope="props">
          <el-form label-position="left" inline class="developList-expand">
            <el-form-item label="属主/数据库">
              <span>{{ props.row.owner }}</span>
            </el-form-item>
            <el-form-item label="对象类型">
              <span>{{ props.row.objectType }}</span>
            </el-form-item>
            <el-form-item label="数据量">
              <span>{{ props.row.dataVolume }}</span>
            </el-form-item>
          </el-form>
        </template>
      </el-table-column>
      <el-table-column
        type="selection"
        width="55">
      </el-table-column>
      <el-table-column
        prop="sourceObjectInfo.databaseType"
        label="数据库类型"
        width="120"
        :filters="[{text: 'mysql', value: 'mysql'}, {text: 'oracle', value: 'oracle'}]"
        :filter-method="filterHandler">
      </el-table-column>
      <el-table-column
        prop="sourceObjectInfo.sysAbbreviation"
        label="系统名称"
        width="180">
      </el-table-column>
      <el-table-column
        prop="sourceObjectInfo.owner"
        label="schema"
        width="100">
      </el-table-column>
      <el-table-column
        prop="sourceObjectInfo.objectName"
        label="对象名称"
        width="180">
      </el-table-column>
      <el-table-column
        prop="createDt"
        label="创建时间"
        width="180">
      </el-table-column>
      <el-table-column
        prop="createBy"
        label="创建人"
        width="100">
      </el-table-column>
      <el-table-column
        label="操作"
        width="100">
        <template slot-scope="scope">
        <el-button
          size="mini"
          type="primary"
          class="el-icon-edit"
          @click="handleEdit(scope.$index, scope.row)">
        </el-button>
        </template>
      </el-table-column>
    </el-table>

    <el-pagination class="pagination"
                   background
                   layout="prev, pager, next"
                   :total="1000">
    </el-pagination>
  </div>
</template>
<script>
  export default {
    name:'toBeDevelopList',
    data () {
      var multipleSelection;
      var searchString;
      var toBeDevelopList;
      return {
        multipleSelection,
        searchString,
        toBeDevelopList,
      }
    },
    components:{
    },
    created(){
      this.getDevelopList()
    },
    methods:{
      cellStyle({row, column, rowIndex, columnIndex}){
        return 'padding:3px;font-size:7px'
      },
      headerCellStyle({row, column, rowIndex, columnIndex}){
        return 'padding:3px;font-size:14px'
      },
      filterHandler(value, row, column) {
        const property = column['property'];
        return row[property] === value;
      },
      handleCommand(command) {
        if(command=='addToDevelopList'){

        }else if(command=='getDataVolume'){

        }
      },
      handleEdit(index, row) {
        let tableInfo=JSON.stringify(row)
        console.log(tableInfo)
        this.$router.push({
          name:'DevelopDashboard',
          params: {
            tableInfo: tableInfo
          }
        })
      },
      clickRow(row){

      },
      handleSelectionChange(val) {
        this.multipleSelection = val;
      },
      getDevelopList(){
        this.$axios.post('/developInfo/getDevelopList',
          {developStatus:"待开发"})
          .then(response =>{
            this.toBeDevelopList = response.data.msg
            console.log(this.toBeDevelopList)
          })
      },
    }
  }
</script>
<style>
  .table{
    position: relative;
    height:100%;
  }
  .tool-bar{
    position: relative;
    height: 30px;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
  }
  .search{
    position: relative;
    width: 280px;
  }
  .el-dropdown-link {
    cursor: pointer;
    color: #409EFF;
  }
  .el-icon-arrow-down {
    font-size: 12px;
  }
  .demonstration {
    display: block;
    color: #8492a6;
    font-size: 14px;
    margin-bottom: 20px;
  }
  .pagination{
    position: fixed;
    margin-bottom: 20px;
  }
  .developList-expand {
    width: 100%;
    display: flex;
    flex-direction: column;
    text-align: center;
  }
  .search-input{
    width: 60%;
  }
  .el-form-item{
    display: flex;
    flex-direction: row;
    text-align: left;
  }
  .el-form-item__label{
    color: #99a9bf;
    font-size: 10px;
  }
  .el-form-item__content {
    color: #99a9bf;
    font-size: 10px;
  }
</style>



