<template>
  <div class="table">
    <div class="tool-bar">
      <div class="search">
        <el-input class="search-input" size="mini" placeholder="请输入内容" prefix-icon="el-icon-search" v-model="searchString" clearable>
        </el-input>
        <el-button type="primary" size="mini" icon="el-icon-search">查询</el-button>
      </div>
      <!--<el-button type="primary" plain @click="getDataVolume()">查询数据量</el-button>-->
      <!--<el-button type="primary" plain @click="addToDevelopList()">加入开发列表</el-button>-->
      <el-dropdown @command="handleCommand">
      <span class="el-dropdown-link">
        操作列表<i class="el-icon-arrow-down el-icon--right"></i>
      </span>
        <el-dropdown-menu slot="dropdown">
          <el-dropdown-item command="getMetaData" icon="el-icon-plus">查询元数据</el-dropdown-item>
          </el-dropdown-menu>
      </el-dropdown>
    </div>
    <hr>
    <el-table
    :data="sourceObjectList"
    height="90%"
    style="width: 100%;position:relative;"
    @row-click="clickRow"
    ref="sourceObjectList"
    @selection-change="handleSelectionChange"
    :cell-style="cellStyle"
    :header-cell-style="headerCellStyle">
    <el-table-column
      type="selection"
      width="55">
    </el-table-column>
      <el-table-column :show-overflow-tooltip="true"
        prop="databaseType"
        label="数据库类型"
        width="100">
      </el-table-column>
    <el-table-column :show-overflow-tooltip="true"
      prop="sysAbbreviation"
      label="系统名称"
      width="120">
    </el-table-column>
    <el-table-column :show-overflow-tooltip="true"
      prop="objectName"
      label="对象名称"
      width="180">
    </el-table-column>
    <el-table-column :show-overflow-tooltip="true"
      prop="createDt"
      label="创建时间"
      width="180">
    </el-table-column>
    <el-table-column :show-overflow-tooltip="true"
      prop="createBy"
      label="创建人"
      width="120">
    </el-table-column>
    <el-table-column :show-overflow-tooltip="true"
      prop="pic"
      label="负责人"
      width="120">
    </el-table-column>
    <!--<el-table-column label="操作">-->
      <!--<template slot-scope="scope">-->
        <!--<el-button-->
          <!--size="mini"-->
          <!--@click="toDevelopDashboard(scope.$index, scope.row)">开发</el-button>-->
      <!--</template>-->
    <!--</el-table-column>-->
  </el-table>

  <el-pagination class="pagination"
    background
    layout="prev, pager, next"
    :total="1000">
  </el-pagination>
  </div>
</template>
<script>
  import eventBus from '@/assets/eventBus.js'
  export default {
    name:'sourceObjectList',
    data () {
      var sourceObjectList=[];
      var searchString;
      var multipleSelection;
      return {
        sourceObjectList,
        searchString,
        multipleSelection,
      }
    },
    components:{
    },
    created(){
      this.getSourceObjectList();
    },
    methods:{
      cellStyle({row, column, rowIndex, columnIndex}){
        return 'padding:3px;font-size:7px'
      },
      headerCellStyle({row, column, rowIndex, columnIndex}){
        return 'padding:3px;font-size:14px'
      },
      getSourceObjectList(){
        this.$axios.post('/sourceObjectInfo/getSourceObjectListByLevel',
          {level:1})
          .then(response =>{
            console.log(response)
            this.sourceObjectList = JSON.parse(response.data.msg)
          })
      },
      clickRow(row){
        this.$refs.sourceObjectList.toggleRowSelection(row)
      },
      handleSelectionChange(val) {
        this.multipleSelection = val;
        // eventBus.$emit('to-getMetaData',this.multipleSelection)
      },
      handleCommand(command) {
        if(command=='getMetaData'){
          this.getMetaData()
        }else if(command=='getDataVolume'){

        }
      },
      getMetaData(){
        var tableList=JSON.stringify(this.multipleSelection)
        if(tableList){
          this.$axios.post('/dataInvestigate/getObjectownerAndTypeAndUniqueName',
            {tableList:tableList})
            .then(response =>{
              console.log(response)
              this.$message({
                message: '查询成功!',
                type: 'success'
              });
            })
        }else {
          this.$message({
            message: '请选择要查询的表',
            type: 'warning'
          });
        }

      }
      // toDevelopDashboard(index,row){
      //   // if(this.$refs[timelineDialog]){
      //   //   this.$refs.timelineDialog.initForm(row);
      //   // }
      //   this.row=row
      //   this.dialogVisible= true;
      // }
    }
  }
</script>
<style>
  .pagination{
    position: fixed;
    margin-bottom: 20px;
  }
  .developList-expand {
    width: 100%;
    display: flex;
    flex-direction: column;
    text-align: center;
  }
  .el-form-item{
    display: flex;
    flex-direction: row;
    text-align: left;
  }
  .el-form-item__label{
    color: #99a9bf;
    font-size: 14px;
  }
 .el-form-item__content {
   color: #99a9bf;
    font-size: 14px;
  }
  .table{
    position: relative;
    height:100%;
  }
</style>



