<template>
  <el-tabs v-model="activeName" @tab-click="handleClick">
    <el-tab-pane label="客户清单" name="sourceObjectList">
      <source-object-list ref="sourceObjectList"></source-object-list>
    </el-tab-pane>
    <el-tab-pane label="元数据" name="metaDataList">
      <meta-data-list ref="metaDataList"></meta-data-list>
    </el-tab-pane>
  </el-tabs>
</template>
<script>
  import SourceObjectList from '@/view/manage/SourceObjectList'
  import MetaDataList from '@/view/metaData/MetaDataList'
  export default {
    name:'objectList',

    components:{
      SourceObjectList,
      MetaDataList,
    },
    data() {
      return {
        activeName: 'sourceObjectList'
      };
    },
    methods: {
      handleClick(tab, event) {
        // console.log(tab, event);
        if(tab.name=='sourceObjectList'){
          this.$refs.sourceObjectList.getSourceObjectList();
        }else if(tab.name=='metaDataList'){
          // console.log(this.$refs.metaDataList)
          this.$refs.metaDataList.getMetaDataList();
        }
      }
    }
  }
</script>
<style>
  .el-tabs {
    position: relative;
    height: 100%;
  }
  .el-tabs__content{
    position: relative;
    height: 80%;
  }
  .el-tab-pane{
    position: relative;
    height: 100%;
  }
</style>



