<template>
  <el-tabs v-model="activeName" @tab-click="handleClick">
    <el-tab-pane label="待开发列表" name="toBeDevelopList">
      <to-be-develop-list ref="toBeDevelopList"></to-be-develop-list>
    </el-tab-pane>
    <el-tab-pane label="已开发列表" name="developList">
      <develop-list ref="developList"></develop-list>
    </el-tab-pane>
    <!--<el-tab-pane label="角色管理" name="third">角色管理</el-tab-pane>-->
    <!--<el-tab-pane label="定时任务补偿" name="fourth">定时任务补偿</el-tab-pane>-->
  </el-tabs>
</template>
<script>
  import ToBeDevelopList from '@/view/develop/toBeDevelopList'
  import DevelopList from '@/view/develop/developList'
  export default {
    name:'developPage',
    components:{
      DevelopList,
      ToBeDevelopList,
    },
    data() {
      return {
        activeName: 'toBeDevelopList'
      };
    },
    methods: {
      handleClick(tab, event) {
        // console.log(tab, event);
        if(tab.name=='sourceObjectList'){
          this.$refs.sourceObjectList.getSourceObjectList();
        }else if(tab.name=='metaDataList'){
          // console.log(this.$refs.metaDataList)
          this.$refs.metaDataList.getMetaDataList();
        }
      }
    }
  }
</script>
<style>
  .el-tabs {
    position: relative;
    height: 100%;
  }
  .el-tabs__content{
    position: relative;
    height: 80%;
  }
  .el-tab-pane{
    position: relative;
    height: 100%;
  }
</style>



