<template>
  <div class="table">
    <div class="tool-bar">
      <div class="search">
      <el-input class="search-input" size="mini" placeholder="请输入内容" prefix-icon="el-icon-search" v-model="searchString" clearable>
      </el-input>
      <el-button type="primary" size="mini" icon="el-icon-search">查询</el-button>
      </div>
        <!--<el-button type="primary" plain @click="getDataVolume()">查询数据量</el-button>-->
        <!--<el-button type="primary" plain @click="addToDevelopList()">加入开发列表</el-button>-->
      <el-dropdown @command="handleCommand">
      <span class="el-dropdown-link">
        操作列表<i class="el-icon-arrow-down el-icon--right"></i>
      </span>
        <el-dropdown-menu slot="dropdown">
          <el-dropdown-item command="addToDevelopList" icon="el-icon-plus">加入开发列表</el-dropdown-item>
          <el-dropdown-item command="getDataVolume" icon="el-icon-circle-plus">查询数据量</el-dropdown-item>
          <!--<el-dropdown-item icon="el-icon-circle-plus-outline">螺蛳粉</el-dropdown-item>-->
          <!--<el-dropdown-item icon="el-icon-check">双皮奶</el-dropdown-item>-->
          <!--<el-dropdown-item icon="el-icon-circle-check-outline">蚵仔煎</el-dropdown-item>-->
        </el-dropdown-menu>
      </el-dropdown>
    </div>
    <hr>
  <el-table
    :data="metaDataList"
    height="90%"
    style="width: 100%"
    @row-click="clickRow"
    ref="metaDataList"
    @selection-change="handleSelectionChange"
    :cell-style="cellStyle"
    :header-cell-style="headerCellStyle">
    <el-table-column type="expand">
      <template slot-scope="props">
        <el-form label-position="left" inline class="developList-expand">
          <el-form-item label="属主/数据库">
            <span>{{ props.row.owner }}</span>
          </el-form-item>
          <el-form-item label="对象类型">
            <span>{{ props.row.objectType }}</span>
          </el-form-item>
          <el-form-item label="数据量">
            <span>{{ props.row.dataVolume }}</span>
          </el-form-item>
        </el-form>
      </template>
    </el-table-column>
    <el-table-column
      type="selection"
      width="55">
    </el-table-column>
    <el-table-column
      prop="databaseType"
      label="数据库类型"
      width="120"
      :filters="[{text: 'mysql', value: 'mysql'}, {text: 'oracle', value: 'oracle'}]"
      :filter-method="filterHandler">
    </el-table-column>
    <el-table-column
      prop="sysAbbreviation"
      label="系统名称"
      width="180">
    </el-table-column>
    <el-table-column
      prop="objectName"
      label="对象名称"
      width="180">
    </el-table-column>
    <el-table-column
    prop="owner"
    label="schema"
    width="100">
  </el-table-column>
    <el-table-column
      prop="objectType"
      label="对象类别"
      width="100">
    </el-table-column>
    <el-table-column
      prop="dataVolume"
      label="数据量"
      width="100">
    </el-table-column>
    <el-table-column
      prop="developStatus"
      label="开发状态"
      width="100">
      <template slot-scope="scope">
        <el-tag
          size="mini"
          :type="scope.row.developStatus === '待开发' ? 'warning' : 'info'"
          disable-transitions>{{scope.row.developStatus}}</el-tag>
      </template>
    </el-table-column>
    <!--<el-table-column label="操作">-->
      <!--<template slot-scope="scope">-->
        <!--<el-button-->
          <!--size="mini"-->
          <!--@click="toDevelopDashboard(scope.$index, scope.row)">开发</el-button>-->
      <!--</template>-->
    <!--</el-table-column>-->
  </el-table>

  <el-pagination class="pagination"
    background
    layout="prev, pager, next"
    :total="1000">
  </el-pagination>
  </div>
</template>
<script>
  import eventBus from '@/assets/eventBus.js'
  export default {
    name:'metaDataList',
    data () {
      var metaDataList=[];
      var multipleSelection;
      var searchString;
      return {
        metaDataList,
        multipleSelection,
        searchString,
      }
    },
    components:{
    },
    created(){
      this.getMetaDataList();
    },
    methods:{
      cellStyle({row, column, rowIndex, columnIndex}){
        return 'padding:3px;font-size:7px'
      },
      headerCellStyle({row, column, rowIndex, columnIndex}){
        return 'padding:3px;font-size:14px'
      },
      filterHandler(value, row, column) {
        const property = column['property'];
        return row[property] === value;
      },
      getMetaDataList(){
        this.$axios.post('/sourceObjectInfo/getSourceObjectListByLevel',
          {level:2})
          .then(response =>{
            console.log(response)
            this.metaDataList = JSON.parse(response.data.msg)
          })
      },
      handleCommand(command) {
        if(command=='addToDevelopList'){
          this.addToDevelopList()
        }else if(command=='getDataVolume'){
          this.getDataVolume()
        }
      },
      clickRow(row){
        this.$refs.metaDataList.toggleRowSelection(row)
      },
      handleSelectionChange(val) {
        this.multipleSelection = val;
        eventBus.$emit('to-getDataVolume',this.multipleSelection)
      },
      addToDevelopList(){
        var tableInfos=JSON.stringify(this.multipleSelection)
        if(tableInfos){
          this.$axios.post('/sourceObjectInfo/addToDevelopList',
            {tableInfos:tableInfos})
            .then(response =>{
              this.getMetaDataList()
              this.toggleSelection()
            })
        }else{
          this.$message({
            message: '请选择要添加的表',
            type: 'warning'
          });
        }
      },
      getDataVolume(){
        var tableInfos=JSON.stringify(this.multipleSelection)
        if(tableInfos){
          this.$axios.post('/dataInvestigate/getDataVolume',
            {tableInfos:tableInfos})
            .then(response =>{
              this.getMetaDataList()
              this.toggleSelection()
            })
        }else{
          this.$message({
            message: '请选择要查询的表',
            type: 'warning'
          });
        }
      },
      toggleSelection(rows) {
        if (rows) {
          rows.forEach(row => {
            this.$refs.metaDataList.toggleRowSelection(row);
          });
        } else {
          this.$refs.metaDataList.clearSelection();
        }
      },
      // toDevelopDashboard(index,row){
      //   // if(this.$refs[timelineDialog]){
      //   //   this.$refs.timelineDialog.initForm(row);
      //   // }
      //   this.row=row
      //   this.dialogVisible= true;
      // }
    }
  }
</script>
<style>
  .table{
    position: relative;
    height:100%;
  }
  .tool-bar{
    position: relative;
    height: 30px;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
  }
  .search{
    position: relative;
    width: 280px;
  }
  .el-dropdown-link {
    cursor: pointer;
    color: #409EFF;
  }
  .el-icon-arrow-down {
    font-size: 12px;
  }
  .demonstration {
    display: block;
    color: #8492a6;
    font-size: 14px;
    margin-bottom: 20px;
  }
  .pagination{
    position: fixed;
    margin-bottom: 20px;
  }
  .developList-expand {
    width: 100%;
    display: flex;
    flex-direction: column;
    text-align: center;
  }
  .search-input{
    width: 60%;
  }
  .el-form-item{
    display: flex;
    flex-direction: row;
    text-align: left;
  }
  .el-form-item__label{
    color: #99a9bf;
    font-size: 10px;
  }
 .el-form-item__content {
   color: #99a9bf;
    font-size: 10px;
  }
</style>



