<template>
  <div class="develop-dashboard flex-column">
    <div class="dev-header flex-column">
      <div class="table-info">
        <div></div>
      </div>
      <div class="table-info"></div>
    </div>
    <div class="dev-container flex-row">
      <div class="dev-source flex-column">
        <div class="source-item">系统名称: {{ sourceObjectInfo.sysAbbreviation }}</div>
        <div class="source-item">对象名称: {{ sourceObjectInfo.objectName }}</div>
        <div class="source-item">对象类型: {{ sourceObjectInfo.objectType }}</div>
        <div class="source-item">数据量: {{ sourceObjectInfo.dataVolume }}</div>
      </div>
      <div class="dev-target flex-column">
        <div class="dev- flex-row">
        <div class="target-group flex-row">
          <div class="target-item">目标库名:</div>
          <el-select v-model="targetInfo.schemaDef" placeholder="请选择" size="mini">
            <el-option
              v-for="item in targetInfo.schemas"
              :key="item"
              :label="item"
              :value="item">
            </el-option>
          </el-select>
        </div>
        <div class="target-group flex-row">
          <div class="target-item">目标表名:</div>
          <el-input
            size="mini"
            placeholder="请输入内容"
            style="width: 20%;"
            v-model="targetInfo.tableDef">
          </el-input>
        </div>
        <div class="target-group flex-row">
          <div class="target-item">增/全量策略:</div>
          <el-select v-model="targetInfo.extractTypeDef" placeholder="请选择" size="mini">
            <el-option
              v-for="item in targetInfo.extractType"
              :key="item"
              :label="item"
              :value="item">
            </el-option>
          </el-select>
        </div>
        </div>
        <div class="target-item">选择抽取字段</div>
        <div style="text-align: center">
          <el-transfer
            style="text-align: left; display: inline-block"
            v-model="columnsDef"
            filterable
            :left-default-checked="leftDef"
            :right-default-checked="rightDef"
            :titles="['不抽取字段', '抽取字段']"
            :button-texts="['删除', '添加']"
            :format="{
        noChecked: '${total}',
        hasChecked: '${checked}/${total}'
      }"
            @change="handleChange"
            :data="columns">
            <span slot-scope="{ option }">{{ option.key }} - {{ option.label }}</span>
            <el-button class="transfer-footer" slot="left-footer" size="small">操作</el-button>
            <el-button class="transfer-footer" slot="right-footer" size="small">操作</el-button>
          </el-transfer>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
  export default {
    data() {
      var tableInfo={};
      var sourceObjectInfo;
      var targetInfo={
        schemaDef:'surging',
        schemas:[],
        extractType:['increment','full'],
        extractTypeDef:'',
      };
      var hiveSchemas;
      var columnsDef;
      var columns=[];
      var leftDef=[];
      var rightDef=[];
      return {
        tableInfo,
        sourceObjectInfo,
        hiveSchemas,
        columns,
        columnsDef,
        targetInfo,
        leftDef,
        rightDef,
      }
    },
    created(){
      this.getParams()
      this.getHiveSchemas()
      this.getColumns()
    },
    methods: {
      getParams(){
        // 取到路由带过来的参数
        if(this.$route.params.tableInfo){
          var tableInfo=JSON.parse(this.$route.params.tableInfo)
          this.tableInfo=tableInfo
          this.sourceObjectInfo=tableInfo.sourceObjectInfo
          var uniqueName=tableInfo.sourceObjectInfo.uniqueName
        }else {
          this.$router.push({
            name:'DevelopPage',
          })
        }

        // var sourceObjectInfo=tableInfo.sourceObjectInfo
        // this.tableInfo = tableInfo
        // this.sourceObjectInfo=sourceObjectInfo
        // this.targetInfo.tableDef=sourceObjectInfo.sysAbbreviation.toLowerCase()+'_'+sourceObjectInfo.objectName.toLowerCase()

        this.$axios.post('/developInfo/getDevelopInfoByUniqueName',{
          uniqueName:uniqueName
        })
          .then(response =>{
            console.log(response.data.msg)
            this.tableInfo=response.data.msg
            this.sourceObjectInfo=response.data.msg.sourceObjectInfo
          })
      },
      handleChange(value, direction, movedKeys) {
        console.log(value, direction, movedKeys);
      },
      getHiveSchemas(){
        this.$axios.post('/hive/getSchemas')
          .then(response =>{
            this.targetInfo.schemas=response.data.msg
            console.log(this.targetInfo.schemas)
          })
      },
      getColumns(){
        console.log(this.tableInfo)
        this.$axios.post('/sourceStructure/getStructureByUniqueName',{
          uniqueName:this.tableInfo.sourceObjectInfo.uniqueName
        })
          .then(response =>{
            var columns=response.data.msg
            var data=[]
            var dataDef=[]
            for(var i=0;i<columns.length;i++){
              data.push({
                key: i,
                label: columns[i].columnName+' --'+columns[i].columnType
              });
              dataDef.push(i)
            }
            this.columns=data
            this.columnsDef=dataDef
          })
      }
    }
  }
</script>
<style>
  .flex-column{
    position: relative;
    display: flex;
    flex-direction: column;
  }
  .flex-row{
    position: relative;
    display: flex;
    flex-direction: row;
  }
  .develop-dashboard{
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 100%;
  }
  .dev-header{
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 10%;
  }
  .dev-container{
    width: 100%;
    height: 90%;
  }
  .dev-source{
    width: 20%;
    height: 100%;
    margin-right: 10px;
    border-right: 1px solid #C0C0C0;
  }
  .source-item{
    width: 100%;
    height: 20px;
    font-size: 12px;
  }
  .dev-target{
    width: 80%;
    height: 100%;
  }
  .target-item{
    margin-right: 10px;
  }
  .el-transfer-panel{
    width: 300px;
  }
  .el-checkbox__label{
    font-size: 6px;
  }
</style>



